}

img {
  max-width: 100%;
  border-radius: 5px;
  display: block;
}

.search-form {
  background-color: #5e41d4;
  justify-content: center;
  margin-bottom: 24px;
  padding: 8px;
  display: flex;
}

.button {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.button.is-hidden, .button > .spinner.is-hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
